import React from "react";
import Preloader from "./Preloader";

const Layout = ({ children }) => {
  // Preloader
  const [loader, setLoader] = React.useState(true);

  React.useEffect(() => {
    setTimeout(() => setLoader(false), 3000);
  }, []);

  return (
    <React.Fragment>
      {children}
      {loader ? <Preloader /> : null}
    </React.Fragment>
  );
};

export default Layout;
