import React, { Component } from "react";
import pic_1 from "../img/works/1.png";
import pic_3 from "../img/works/3.png";
import pic_5 from "../img/works/5.png";
import Popup from "../components/Popup";
import Slider from "../components/Slider";
import slides from "../components/slides";
import amazon from "../img/works/Amazon.png";
import music from "../img/works/music.png";
import bar from "../img/works/bar.png";
import line from "../img/works/line-chart.png";
import stack from "../img/works/stacked.png";
import spline from "../img/works/spline-chart.png";
import sidebar from "../img/works/sidebar.jpg";
import sidebar2 from "../img/works/sidebar.png";
import stars from "../img/works/sddefault.jpg";
import float from "../img/works/Floating.png";
import quote from "../img/works/react/2.PNG";
import amazon2 from "../img/works/react/1.PNG";
import drum from "../img/works/react/3.PNG";
import d3l from "../img/works/react/Untitled.png";
import d3s from "../img/works/react/Capture11.PNG";
import d3a from "../img/works/react/Capture22.PNG";
import d3b from "../img/works/react/Untitled2.png";
import happy from "../img/works/happy.png";
import magnific from "../img/works/magnific.png";

class Works extends Component {
  constructor(props) {
    super(props);

    this.initialState = {
      value: "All",
      numberItems: 6,
      // with initial state, you can keep it as an original copy, so when the state changed, you can still visit this to get an origional data, use this to set back from a filter function
      list: [
        {
          category: "React",
          task:
            "Building a Medical website for stone disease patients and doctors to use",

          img_src: pic_1,
          slide: true,
          content: slides[0],
        },
        {
          category: "React",
          task: "Building a Real Estate website for printing out PDF to users",

          img_src: pic_3,
          slide: true,
          content: slides[1],
        },
        {
          category: "React",
          task: "Building an Amazon Style Review Component",
          description:
            "Created a review component with Amazon style using React. By clicking on the helpful button, the number of people find it helpful will increase",
          img_src: amazon,
          img: amazon2,
          href: "https://codesandbox.io/s/amazon-review-98x9f",
        },
        {
          category: "D3",
          task: "Bar graph",
          description:
            "A flexible bar graph built using d3, click add dataset or remove dataset to check the effect,hover legend to highlight data series, click legend to delete data series",
          img_src: bar,
          img: d3b,
          href: "https://codepen.io/sashagi/pen/wvWNyom",
        },
        {
          category: "D3",
          task: "Area Chart",
          description:
            "Built an area chart relies on the d3.area() helper function, click add dataset or remove dataset to check the effect",
          img_src: spline,
          img: d3a,
          href: "https://codepen.io/sashagi/pen/rNLPJLE",
        },
        {
          category: "YouTube",
          task: "Simple Sidebar",
          description:
            "Make a video of how to make simple sidebar using bootstrap",
          img_src: sidebar2,
          img: sidebar,
          href: "https://youtu.be/7gS243wGXLk",
          button: "Check out video on YouTube",
        },

        {
          category: "D3",
          task: "Stacked Bar Chart",
          description:
            "A flexible stacked coloumn graph built using d3 stack, click add dataset or remove dataset to check the effect",
          img_src: stack,
          img: d3s,
          href: "https://codepen.io/sashagi/pen/qBNgxNO",
        },

        {
          category: "React",
          task: "Building a Quote machine",
          description: " To display quotes in random order",
          img_src: pic_5,
          img: quote,
          href: "https://codesandbox.io/s/quote-machine-duznf",
        },
        {
          category: "YouTube",
          task: "Magnific Popup",
          description: "Make a video of how to implement Magnific Popup plugin",
          img_src: happy,
          img: magnific,
          href: "https://youtu.be/Uh1JixePqbQ",
          button: "Check out video on YouTube",
        },
        {
          category: "D3",
          task: "Line chart",
          description:
            "Built a line chart relies on the d3.line() helper function, click add dataset,remove dataset or mouse over to check different effects",
          img_src: line,
          img: d3l,
          href: "https://codepen.io/sashagi/pen/zYBeRoO",
        },

        {
          category: "React",
          task: "Building a Drum machine",
          description:
            "By clicking on each card, a different kind of music will be played, and the name of the music will be displayed on the top area.",
          img_src: music,
          img: drum,
          href: "https://codesandbox.io/s/drum-machine-7cjcn",
        },
        {
          category: "YouTube",
          task: "Star animation",
          description:
            "Make a video of how to make twinkling star with Html,CSS and JS",
          img_src: float,
          img: stars,
          href: "https://youtu.be/7gS243wGXLk",
          button: "Check out video on YouTube",
        },
      ],
    };
    this.state = this.initialState;
  }

  filter = (x) => {
    if (x) {
      let filtered = this.initialState.list.filter(
        (value) => value.category === x
      );
      this.setState({ list: filtered }); //setState just need the name after this.state
      // document.getElementById("All").classList.remove("current");
      document.getElementById("invisible").classList.add("invisible");

      let els = document.getElementsByClassName("current");
      for (const el of els) {
        el.classList.remove("current");
      }
      document.getElementById(x).classList.add("current");
    } else {
      this.setState({ list: this.initialState.list });

      let els = document.getElementsByClassName("current");
      for (const el of els) {
        el.classList.remove("current");
      }
      document.getElementById("All").classList.add("current");
      document.getElementById("invisible").classList.remove("invisible");
    }
  };
  change = (event) => {
    this.filter(event.target.value);
  };
  handleShowMore() {
    this.setState({ numberItems: this.state.list.length });
  }
  render() {
    const items = this.state.list.slice(0, this.state.numberItems);
    return (
      <div className="wrapper justify-content-center align-items-center m-3">
        <div className="container-fluid d-flex flex-column align-items-center p-3">
          <h2 className="section-title fadeInUp">Recent works</h2>

          <ul className="portfolio-filter list-inline fadeInUp mb-5">
            <li
              className="current list-inline-item"
              id="All"
              onClick={this.filter.bind(this, null)}
            >
              All
            </li>
            <li
              className="list-inline-item"
              id="React"
              onClick={this.filter.bind(this, "React")} //need to bind this due to js reason
            >
              React
            </li>
            {/* <li
              className="list-inline-item"
              onClick={this.filter.bind(this, "Clones")}
            >
              Clones
            </li> */}
            <li
              className="list-inline-item"
              id="D3"
              onClick={this.filter.bind(this, "D3")}
            >
              D3
            </li>
            <li
              className="list-inline-item"
              id="YouTube"
              onClick={this.filter.bind(this, "YouTube")}
            >
              YouTube
            </li>
          </ul>

          <div className="pf-filter-wrapper">
            <select className="portfolio-filter-mobile" onChange={this.change}>
              <option value="">All</option>
              <option value="React">React</option>
              {/* <option value="Clones">Clones</option> */}
              <option value="D3">D3</option>
              <option value="YouTube">YouTube</option>
            </select>
          </div>

          <div className="row portfolio-wrapper d-flex justify-content-center">
            {items.map((value, index) => (
              <div className="col-xl-5 ml-4 art" key={index}>
                <div className="portfolio-item rounded">
                  <div className="details">
                    <span className="term">{value.category}</span>
                    <h4 className="title">{value.task}</h4>
                    {value.slide ? (
                      <Slider content={value.content} />
                    ) : (
                      <Popup
                        description={value.description}
                        img={value.img}
                        title={value.task}
                        href={value.href}
                        button={value.button}
                      />
                    )}
                  </div>
                  <div className="thumb">
                    <img src={value.img_src} alt="Portfolio-title" />
                    <div className="mask"></div>
                  </div>
                </div>
              </div>
            ))}
          </div>
          <div
            className="load-more pb-5 mt-3"
            onClick={this.handleShowMore.bind(this)}
          >
            <button className="btn btn-default" id="invisible">
              Load more
            </button>
          </div>
        </div>
      </div>
    );
  }
}
export default Works;
