import { Component } from "react";
import Navitem from "./Navitem";
import { BiHomeSmile } from "react-icons/bi";
import { SiAboutDotMe } from "react-icons/si";
import { FaGraduationCap } from "react-icons/fa";
import { CgWorkAlt } from "react-icons/cg";
import { IoIosContacts } from "react-icons/io";

class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      NavItemActive: "",
    };
  }
  activeitem = (x) => {
    const home = document.querySelector(".homeInitial");
    if (this.state.NavItemActive.length > 0) {
      document
        .getElementById(this.state.NavItemActive)
        .classList.remove("active");
    }
    if (home.classList.contains("active")) {
      home.classList.remove("active");
    }
    this.setState({ NavItemActive: x }, () => {
      document.getElementById(this.state.NavItemActive).classList.add("active");
    });
  };

  render() {
    return (
      <div className="border-right d-flex" id="sidebar-wrapper">
        <ul
          id="sidebar-list"
          className="d-flex flex-column justify-content-center"
        >
          <Navitem
            item="Home"
            tolink="/"
            icon={<BiHomeSmile className="mr-3" />}
            active={this.activeitem}
            className="homeInitial active "
          ></Navitem>
          <Navitem
            item="About"
            tolink="/about"
            active={this.activeitem}
            icon={<SiAboutDotMe className="mr-3" />}
          ></Navitem>
          <Navitem
            item="Experience"
            tolink="/experience"
            active={this.activeitem}
            icon={<FaGraduationCap className="mr-3" />}
          ></Navitem>
          <Navitem
            item="Works"
            tolink="/works"
            active={this.activeitem}
            icon={<CgWorkAlt className="mr-3" />}
          ></Navitem>
          <Navitem
            item="Contact"
            tolink="/contact"
            active={this.activeitem}
            icon={<IoIosContacts className="mr-3" />}
          ></Navitem>
        </ul>
      </div>
    );
  }
}

export default Navbar;
