import { useState } from "react";
import { withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import MuiDialogTitle from "@material-ui/core/DialogTitle";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import Typography from "@material-ui/core/Typography";

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: "absolute",
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton
          aria-label="close"
          className={classes.closeButton}
          onClick={onClose}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

export default function Slider(props) {
  const [open, setOpen] = useState(false);
  let slides = props.content;

  const [state, setState] = useState({
    length: slides.length,
    currentIndex: 0,
  });

  const next = () => {
    state.currentIndex < state.length - 1
      ? setState({ ...state, currentIndex: state.currentIndex + 1 })
      : setState({ ...state, currentIndex: 0 });
  };
  const prev = () => {
    state.currentIndex > 0
      ? setState({ ...state, currentIndex: state.currentIndex - 1 })
      : setState({ ...state, currentIndex: state.length - 1 });
  };

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button
        className="more-button"
        variant="outlined"
        color="primary"
        onClick={handleClickOpen}
      >
        Read More
      </Button>
      <Dialog
        onClose={handleClose}
        aria-labelledby="customized-dialog-title"
        open={open}
        className="MuiDialog-paperWidthXl"
        maxWidth="xl"
      >
        <div className="row d-flex">
          <DialogTitle id="customized-dialog-title" onClose={handleClose}>
            React Website
          </DialogTitle>
          <div className="row col-12 mb-5 d-flex align-items-center">
            <button className="previous" onClick={prev}>
              <i class="fas fa-chevron-left"></i>
            </button>
            <div className="slider d-flex justify-content-center">
              {slides.map((slide, index) => (
                <Slide
                  title={slide.title}
                  image={slide.image}
                  description={slide.description}
                  show={state.currentIndex === index}
                  key={index}
                />
              ))}
            </div>
            <button className="next" onClick={next}>
              <i class="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      </Dialog>
    </div>
  );
}
const Slide = (props) => {
  let classString = props.show ? "slide show" : "slide";
  return (
    <div className={classString}>
      <h1> {props.title} </h1>
      <img src={props.image} alt="works" />
      <p> {props.description} </p>
    </div>
  );
};
