import React from "react";

var num = 8, //change this for number of circles
  ang = 360 / num,
  rad = num * 6,
  run = 2;

const Preloader = () => {
  const buttons = [];
  for (let i = 0; i < num; i++) {
    const style = {
      top: rad * Math.cos((ang * i * Math.PI) / 180) - 10 + "px",
      left: rad * Math.sin((ang * i * Math.PI) / 180) - 10 + "px",
      backgroundColor: "hsla(" + ang * i + ", 50%, 50%, 1)",
      animation:
        "osc " +
        run +
        "s ease-in-out infinite " +
        i / (num / 2) +
        "s, rainbow 5s infinite linear " +
        i / (num / 2) +
        "s",
    };
    buttons.push(<div key={i} className={"dot" + i + " dot"} style={style} />);
  }

  return (
    <div id="preloader">
      <div id="hold">{buttons}</div>
    </div>
  );
};

export default Preloader;

