import { useRef, useEffect } from "react";
import { gsap } from "gsap";
import flower from "../img/works/flower.png";

const Flower = () => {
  const refs = useRef([]);
  const numberOfFlowers = 10;
  const flowers = [];

  for (let i = 0; i < numberOfFlowers; i++) {
    const style = "hsla(" + 12 * i + ", 70%, 80%, 0.5)";
    flowers.push(
      <img
        src={flower}
        key={i}
        className="flower"
        ref={(el) => (refs.current[i] = el)}
        style={{ color: style }}
        alt=""
      />
    );
  }
  useEffect(() => {
    for (let i = 0; i < numberOfFlowers; i++) {
      animateBlobs(refs.current[i]);
    }
  });

  function animateBlobs(n) {
    let xSeed = Math.floor(Math.random() * 60 + 450);
    let ySeed = Math.floor(Math.random() * 50 + 220);
    let speed = Math.floor(Math.random() * 4 + 1);
    let rotation = Math.floor(Math.random() * 95 + 5);
    let scale = (Math.random() * 0.7 + 0.8).toFixed(1);
    let x = Math.floor(Math.random() * xSeed * 2 - xSeed);
    let y = Math.floor(Math.random() * ySeed * 2 - ySeed);
    gsap.to(n, speed, {
      x: x,
      y: y,
      ease: "power1.inOut",
      opacity: 0,
      rotation: rotation,
      scale: scale,
    });
  }

  return <div className="popup">{flowers}</div>;
};

export default Flower;
